
body,
html,
.app {
  height: 100%;
  padding: 0;
  margin: 0;
  background-color: #f3f4f7;
  font-family: Helvetica, Arial;
}
* {
  box-sizing: border-box;
}
.links {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  margin-left: -65px;
  margin-right: 260px;
  white-space: nowrap;
}
.links > div {
  padding: 29px 27px;
}
.heading-bar {
  border-top: 7px solid #000;
  clear: both;
}
.login-bar {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  padding-right: 20px;
}
.login-bar .user-name {
  white-space: nowrap;
}
.exit-button {
  text-align: right;
  border-left: 1px solid #e8e8e8;
  padding: 28px 0 20px 40px;
  margin-left: 27px;
  font-size: 20px;
}
.heading-white {
  background-color: #fff;
  box-shadow: 0px 2px 9px 0px rgba(0,0,0,0.16);
}
.heading-white-content {
  max-width: 1096px;
  margin: 0 auto;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 40px;
  height: 80px;
}
.gray-text {
  color: #bec2c2;
}
.gray-text-position {
  position: relative;
  top: -5px;
  left: -2px;
}
.heading-transparent {
  padding: 14px 40px;
  font-size: 12px;
  width: 1096px;
  margin: 0 auto;
}
.footer {
  background-color: #000;
}
.footer-content {
  max-width: 1096px;
  margin: 0 auto;
  padding: 20px 0;
  color: #949494;
  font-size: 12px;
}
.heading-personal-manager {
  margin-right: 38px;
}
.heading-personal-phone {
  margin-right: 26px;
}
.text-with-icon {
  margin-left: 7px;
}
.logo-holder {
  position: relative;
}
.logo-subtext {
  position: absolute;
  top: 42px;
}


